.modal-movie {
  opacity: 1;
  width: 280px;
  max-height: 95vh;
  height: auto;
  background: var(--body-and-modals-bgc);
  border-radius: 5px;
  padding: 48px 20px 40px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
